ofile-form\">\r\n            <div class=\"row\">\r\n                <h2 id=\"ven-profile-form-head\">PROFILE</h2>\r\n\r\n                <div class=\"vendor-logo-upload\">\r\n                    <label v-if=\"!personalfileOpen\" for=\"company-logo\">\r\n                        <img\r\n                            v-if=\"companyPicPreview\"\r\n                            :src=\"companyPicPreview\"\r\n                            alt=\"\"\r\n                        />\r\n                        <img\r\n                            v-else-if=\"companyProfile.imageOne\"\r\n                            :src=\"\r\n                                '/assets/vendors/uploads/' +\r\n                                companyProfile.imageOne\r\n                            \"\r\n                            alt=\"\"\r\n                        />\r\n                        <img\r\n                            v-else\r\n                            src=\"/assets/vendors/dashboard/ven-logo-upload.png\"\r\n                            alt=\"\"\r\n                        />\r\n                        <span>Add</span>\r\n                        <span\r\n                            v-if=\"errors && errors.companyimg\"\r\n                            class=\"errMsg\"\r\n                            >{{ errors.companyimg[0] }}</span\r\n                        >\r\n                    </label>\r\n                    <label v-if=\"personalfileOpen\" for=\"vendor-logo\">\r\n                        <img\r\n                            v-if=\"profilePicPreview\"\r\n                            :src=\"profilePicPreview\"\r\n                            alt=\"\"\r\n                        />\r\n                        <img\r\n                            v-else-if=\"sendProfile.imageOne\"\r\n                            :src=\"\r\n                                '/assets/vendors/uploads/' +\r\n                                sendProfile.imageOne\r\n                            \"\r\n                            alt=\"\"\r\n                        />\r\n                        <img\r\n                            v-else\r\n                            src=\"/assets/vendors/dashboard/ven-logo-upload.png\"\r\n                            alt=\"\"\r\n                        />\r\n                        <span v-if=\"errors && errors.img\" class=\"errMsg\">{{\r\n                            errors.img[0]\r\n                        }}</span>\r\n                    </label>\r\n                </div>\r\n            </div>\r\n            <div :class=\"[loader ? 'blur row about' : 'row about']\">\r\n                <div class=\"btn-container\">\r\n                    <button\r\n                        type=\"button\"\r\n                        class=\"tab-btn active\"\r\n                        data-id=\"company-details\"\r\n                        title=\"Company Profile\"\r\n                        @click=\"\r\n                            toggleForm($event, true);\r\n                            personalfileOpen = false;\r\n                        \"\r\n                    ></button>\r\n                    <button\r\n                        type=\"button\"\r\n                        class=\"tab-btn\"\r\n                        data-id=\"vendor-details\"\r\n                        title=\"Personal Profile\"\r\n                        @click=\"\r\n                            toggleForm($event, true);\r\n                            personalfileOpen = true;\r\n                        \"\r\n                    ></button>\r\n                </div>\r\n                <div class=\"about-content\">\r\n                    <form\r\n                        @submit.prevent=\"submitCProfile($event)\"\r\n                        class=\"content active\"\r\n                        id=\"company-details\"\r\n                    >\r\n                        <input\r\n                            type=\"file\"\r\n                            id=\"company-logo\"\r\n                            name=\"company-logo\"\r\n                            required\r\n                            style=\"\r\n                                left: 42%;\r\n                                position: absolute;\r\n                                top: 13%;\r\n                                opacity: 0;\r\n                            \"\r\n                            @change=\"uploadImg($event, 'company')\"\r\n                        />\r\n                        <h1>Company details</h1>\r\n                        <div\r\n                            v-if=\"cexist == 1\"\r\n                            style=\"display: flex; justify-content: flex-end\"\r\n                        >\r\n                            <button\r\n                                @click=\"editForm($event)\"\r\n                                style=\"\r\n                                    outline: none;\r\n                                    border: none;\r\n                                    background-color: #461952;\r\n                                    color: white;\r\n                                    padding: 4px 20px;\r\n                                    border-radius: 10px;\r\n                                    margin-bottom: 10px;\r\n                                    margin-right: 20px;\r\n                                \"\r\n                                type=\"button\"\r\n                            >\r\n                                Edit\r\n                            </button>\r\n                        </div>\r\n                        <div class=\"vendor-details-form-fields\">\r\n                            <label for=\"companyname\">\r\n                                Company name\r\n                                <input\r\n                                    type=\"text\"\r\n                                    name=\"companyname\"\r\n                                    id=\"companyname\"\r\n                                    placeholder=\"Enter your company name\"\r\n                                    v-model=\"companyProfile.companyname\"\r\n                                    required\r\n                                />\r\n                                <span\r\n                                    v-if=\"errors && errors.companyname\"\r\n                                    class=\"errMsg\"\r\n                                    >{{ errors.companyname[0] }}</span\r\n                                >\r\n                            </label>\r\n                            <label for=\"companyinfo\">\r\n                                Company Description\r\n                                <input\r\n                                    type=\"text\"\r\n                                    name=\"companyinfo\"\r\n                                    id=\"companyinfo\"\r\n                                    placeholder=\"Enter your company description\"\r\n                                    v-model=\"companyProfile.companyinfo\"\r\n                                    required\r\n                                />\r\n                                <span\r\n                                    v-if=\"errors && errors.companyinfo\"\r\n                                    class=\"errMsg\"\r\n                                    >{{ errors.companyinfo[0] }}</span\r\n                                >\r\n                            </label>\r\n                            <label for=\"caddress\">\r\n                                Company Address\r\n                                <input\r\n                                    type=\"text\"\r\n                                    name=\"caddress\"\r\n                                    id=\"caddress\"\r\n                                    placeholder=\"Enter your company address\"\r\n                                    v-model=\"companyProfile.address\"\r\n                                    required\r\n                                />\r\n                                <span\r\n                                    v-if=\"errors && errors.address\"\r\n                                    class=\"errMsg\"\r\n                                    >{{ errors.address[0] }}</span\r\n                                >\r\n                            </label>\r\n                            <label for=\"cmap\">\r\n                                Map\r\n                                <input\r\n                                    type=\"text\"\r\n                                    name=\"cmap\"\r\n                                    id=\"cmap\"\r\n                                    placeholder=\"Map\"\r\n                                    v-model=\"companyProfile.map\"\r\n                                />\r\n                                <span\r\n                                    v-if=\"errors && errors.map\"\r\n                                    class=\"errMsg\"\r\n                                    >{{ errors.map[0] }}</span\r\n                                >\r\n                            </label>\r\n                            <label for=\"latitude\">\r\n                                Latitude\r\n                                <input\r\n                                    type=\"text\"\r\n                                    name=\"latitude\"\r\n                                    id=\"latitude\"\r\n                                    placeholder=\"Latitude\"\r\n                                    v-model=\"companyProfile.latitude\"\r\n                                />\r\n                                <span\r\n                                    v-if=\"errors && errors.latitude\"\r\n                                    class=\"errMsg\"\r\n                                    >{{ errors.latitude[0] }}</span\r\n                                >\r\n                            </label>\r\n                            <label for=\"longitude\">\r\n                                Longitude\r\n                                <input\r\n                                    type=\"text\"\r\n                                    name=\"longitude\"\r\n                                    id=\"longitude\"\r\n                                    placeholder=\"Longtitude\"\r\n                                    v-model=\"companyProfile.longitude\"\r\n                                />\r\n                                <span\r\n                                    v-if=\"errors && errors.longitude\"\r\n                                    class=\"errMsg\"\r\n                                    >{{ errors.longitude[0] }}</span\r\n                                >\r\n                            </label>\r\n                            <label for=\"establish\">\r\n                                Establish\r\n                                <input\r\n                                    type=\"text\"\r\n                                    name=\"establish\"\r\n                                    id=\"establish\"\r\n                                    placeholder=\"Company established\"\r\n                                    v-model=\"companyProfile.establish\"\r\n                                    required\r\n                                />\r\n                                <span\r\n                                    v-if=\"errors && errors.establish\"\r\n                                    class=\"errMsg\"\r\n                                    >{{ errors.establish[0] }}</span\r\n                                >\r\n                            </label>\r\n                            <label for=\"gst\">\r\n                                GST Number\r\n                                <input\r\n                                    type=\"text\"\r\n                                    name=\"gst\"\r\n                                    id=\"gst\"\r\n                                    placeholder=\"GST\"\r\n                                    v-model=\"companyProfile.gst\"\r\n                                    required\r\n                                />\r\n                                <span\r\n                                    v-if=\"errors && errors.gst\"\r\n                                    class=\"errMsg\"\r\n                                    >{{ errors.gst[0] }}</span\r\n                                >\r\n                            </label>\r\n                            <label for=\"phone\">\r\n                                Landline Number\r\n                                <input\r\n                                    type=\"text\"\r\n                                    name=\"whatsapp1\"\r\n                                    id=\"phone\"\r\n                                    placeholder=\"Landline Number\"\r\n                                    v-model=\"companyProfile.phone\"\r\n                                    required\r\n                                />\r\n                                <span\r\n                                    v-if=\"errors && errors.phone\"\r\n                                    class=\"errMsg\"\r\n                                    >{{ errors.phone[0] }}</span\r\n                                >\r\n                            </label>\r\n                        </div>\r\n                        <div class=\"vendor-address-form-fields\">\r\n                            <label for=\"vendorCategories\">\r\n                                Vendor categories\r\n                                <select\r\n                                    name=\"vendorCategories\"\r\n                                    id=\"vendorCategories\"\r\n                                    v-model=\"catId\"\r\n                                    multiple\r\n                                    required\r\n                                >\r\n                                    <option hidden disabled selected>\r\n                                        Select vendor’s category\r\n                                    </option>\r\n                                    <option\r\n                                        v-for=\"(\r\n                                            item, index\r\n                                        ) in defaultCategories\"\r\n                                        :key=\"index\"\r\n                                        :value=\"item.id\"\r\n                                    >\r\n                                        {{ item.name }}\r\n                                    </option>\r\n                                </select>\r\n                                <span\r\n                                    v-if=\"errors && errors.locationmaster_id\"\r\n                                    class=\"errMsg\"\r\n                                    >{{ errors.locationmaster_id[0] }}</span\r\n                                >\r\n                            </label>\r\n                            <label for=\"vendorCategories\">\r\n                                Location\r\n                                <select\r\n                                    name=\"vendorCategories\"\r\n                                    id=\"vendorCategories\"\r\n                                    v-model=\"companyProfile.locationmaster_id\"\r\n                                    required\r\n                                >\r\n                                    <option disabled selected>\r\n                                        Select Location\r\n                                    </option>\r\n                                    <option\r\n                                        v-for=\"(item, index) in locationMaster\"\r\n                                        :key=\"index\"\r\n                                        :value=\"item.id\"\r\n                                    >\r\n                                        {{ item.name }}\r\n                                    </option>\r\n                                </select>\r\n                                <span\r\n                                    v-if=\"errors && errors.locationmaster_id\"\r\n                                    class=\"errMsg\"\r\n                                    >{{ errors.locationmaster_id[0] }}</span\r\n                                >\r\n                            </label>\r\n                            <label for=\"mobile\">\r\n                                Primary Mobile\r\n                                <input\r\n                                    type=\"text\"\r\n                                    name=\"mobile\"\r\n                                    id=\"mobile\"\r\n                                    placeholder=\"Primary Mobile\"\r\n                                    v-model=\"companyProfile.cmobile\"\r\n                                    required\r\n                                />\r\n                                <span\r\n                                    v-if=\"errors && errors.cmobile\"\r\n                                    class=\"errMsg\"\r\n                                    >{{ errors.cmobile[0] }}</span\r\n                                >\r\n                            </label>\r\n                            <!-- <label for=\"mobile1\">\r\n                                Alternate Mobile\r\n                                <input\r\n                                    type=\"text\"\r\n                                    name=\"mobile1\"\r\n                                    id=\"mobile1\"\r\n                                    placeholder=\"Alternate Mobile \"\r\n                                    v-model=\"companyProfile.cmobile1\"\r\n                                    required\r\n                                />\r\n                                <span\r\n                                    v-if=\"errors && errors.cmobile1\"\r\n                                    class=\"errMsg\"\r\n                                    >{{ errors.cmobile1[0] }}</span\r\n                                >\r\n                            </label> -->\r\n                            <label for=\"whatsup\">\r\n  